import React, {useState} from "react";
import { data } from "../utils/data";
import { IoMdArrowDropdown, IoMdArrowDropup } from 'react-icons/io'

export const NetworkTab = () => {
    const [isOpen, setIsOpen] = useState(false)
    return (
        <div className="w-1/6">

            <div className="relative flex flex-col">
                <button onClick={()=>setIsOpen(!isOpen)} className=" w-full p-2 flex items-center  border-black font-bold tracking-wider border-4 border-transparent active:border-black">
                Dropdown
                {
                    !isOpen? <IoMdArrowDropdown/>:<IoMdArrowDropup/>
                }
               
                </button>
            </div>
            <ul className="border-black flex flex-col items-start rounded-lg p-2 w-full ">
                {
                    isOpen && (
                        data.map((item,i)=>{
                            return(
                                <li
                                key={i} 
                                className="flex w-full justify-between border-bl hover:bg-blue-300 cursor-pointer rounded-r-lg border-l-transparent hover:border-l-white border-l-4"
                                > 
                                <img 
                                src={item.icon}
                                alt="icon" 
                                height="20" 
                                width="20"/> {item.name} </li>
                            )
                        })
                    )
                }
                
                </ul> 


        </div>
    )
}



                       